@font-face {
  font-family: "r-bold";
  src: url(../fonts/Roboto-Bold.ttf);
}

@font-face {
  font-family: "r-medium";
  src: url(../fonts/Roboto-Medium.ttf);
}

@font-face {
  font-family: "r-regular";
  src: url(../fonts/Roboto-Regular.ttf);
}

@font-face {
  font-family: "r-thin";
  src: url(../fonts/Roboto-Thin.ttf);
}

@font-face {
  font-family: "r-light";
  src: url(../fonts/Roboto-Light.ttf);
}

.container {
  max-width: 1280px;
}

.mini_container {
  max-width: 1040px;
}

.container,
.mini_container {
  padding: 0px 16px;
  margin: 0 auto;
}

.primaryBtn {
  padding: 8px 16px;
}

.primaryBtnBig {
  padding: 16px 48px;
}

.primaryBtn,
.primaryBtnBig {
  display: block;
  text-decoration: none;
  background: #7140EB;
  font-size: 15px;
  color: #fff;
  letter-spacing: 0.2px;
  width: fit-content;
  border-radius: 999px;
  transition: 0.3s;
}

.primaryBtn:hover,
.primaryBtnBig:hover {
  background: #4f1dcc;
  box-shadow: 0px 0px 10px #4f1dcc;
}

.secondaryBtn {
  padding: 8px 16px;
}

.secondaryBtnBig {
  padding: 16px 48px;
}

.secondaryBtn,
.secondaryBtnBig {
  display: flex;
  align-items: center;
  gap: 4px;
  text-decoration: none;
  background: transparent;
  border: 1px solid #2E2C34;
  color: #2E2C34;
  font-size: 15px;
  cursor: pointer;
  letter-spacing: 0.2px;
  width: fit-content;
  border-radius: 999px;
  transition: 0.3s;
}

.secondaryBtn:hover,
.secondaryBtnBig:hover {
  background: #2E2C34;
  box-shadow: 0px 0px 10px #2E2C34;
  color: #fff;
  transition: 0.3s;
}

.secondaryBtn:hover svg path,
.secondaryBtnBig:hover svg path {
  fill: #fff;
  transition: 0.3s;
}

.light_text_title {
  color: #49464F;
}

.dark_text_title {
  color: #2E2C34;
}

.light_text_title,
.dark_text_title {
  font-weight: 400;
  font-size: min(67px, max(33.5px + 2vw));
}

#burger-menu {
  cursor: pointer;
  height: 27px;
  width: 27px;
  margin: 50px;
  overflow: visible;
  position: relative;
  z-index: 2;
}

#burger-menu span,
#burger-menu span:before,
#burger-menu span:after {
  background: #fff;
  display: block;
  height: 4px;
  opacity: 1;
  position: absolute;
  transition: 0.3s ease-in-out;
}

#burger-menu span:before,
#burger-menu span:after {
  content: "";
}

#burger-menu span {
  right: 0px;
  top: 13px;
  width: 27px;
}

#burger-menu span:before {
  left: 0px;
  top: -10px;
  width: 16px;
}

#burger-menu span:after {
  left: 0px;
  top: 10px;
  width: 20px;
}

#burger-menu.close span {
  transform: rotate(-45deg);
  top: 13px;
  width: 27px;
}

#burger-menu.close span:before {
  top: 0px;
  transform: rotate(90deg);
  width: 27px;
}

#burger-menu.close span:after {
  top: 0px;
  left: 0;
  transform: rotate(90deg);
  opacity: 0;
  width: 0;
}

