header {
  min-height: 800px;
  background-image: url(../../img/header-bg.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

header nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

header nav .nav_left img {
  display: block;
}

header nav .nav_mobile {
  display: none;
}

header nav .nav_right {
  display: flex;
  align-items: center;
  gap: 64px;
}

header nav .nav_right .nav_menu {
  display: flex;
  margin: 0;
}

header nav .nav_right .nav_menu li {
  list-style-type: none;
}

header nav .nav_right .nav_menu li a {
  display: flex;
  align-items: center;
  text-decoration: none;
  color: white;
  min-height: 100px;
  padding: 0px 16px;
  transition: 0.3s;
}

header nav .nav_right .nav_menu li a:hover {
  background: #7140EB;
}

header nav .nav_right .nav_lang {
  display: flex;
  gap: 16px;
}

header nav .nav_right .nav_lang a {
  display: block;
  text-decoration: none;
  color: white;
}

header nav .nav_right .nav_lang a:hover {
  text-decoration: underline;
}

header .header_content {
  max-width: 540px;
  padding: 128px 10px 128px 10px;
  margin: 0 auto;
  text-align: center;
}

header .header_content h1 {
  font-size: min(99px, max(40px + 2vw));
  letter-spacing: 5px;
  color: #fff;
  font-weight: normal;
  margin: 0;
}

header .header_content p {
  margin: 64px 0px;
  font-size: 21px;
  color: white;
  letter-spacing: 0.2px;
}

header .header_content a {
  margin: 0 auto;
  width: 183px;
}

main .how_its_work_section {
  padding: 64px 0px;
  position: relative;
}

main .how_its_work_section .mini_container h2 {
  width: 100%;
  margin: 0px 0px 70px 0px;
}

main .how_its_work_section .mini_container .how_its_work_list {
  position: relative;
  z-index: 5;
}

main .how_its_work_section .mini_container .how_its_work_list ol {
  padding: 0;
  margin-bottom: 48px;
}

main .how_its_work_section .mini_container .how_its_work_list ol li {
  color: #49464F;
  margin-bottom: 24px;
  list-style-position: inside;
  font-size: 28px;
  line-height: 34px;
}

main .how_its_work_section .mini_container .how_its_work_list ol li:last-child {
  margin-bottom: 0px;
}

main .how_its_work_section .mini_container .how_its_work_cover {
  position: absolute;
  z-index: 1;
  top: 70px;
  right: 0;
}

main .how_its_work_section .mini_container .how_its_work_cover img {
  display: block;
}

main .move_your_way_section {
  background: #F5F5F7;
  padding: 64px 0px;
}

main .move_your_way_section h2 {
  margin: 0px 0px 24px 0px;
}

main .move_your_way_section p {
  font-size: 20px;
  line-height: 25px;
  color: #2E2C34;
  margin: 0px;
}

main .move_your_way_items {
  display: flex;
  justify-content: center;
  gap: 16px;
  margin-top: 40px;
}

main .move_your_way_items .move_your_way_item {
  background: #fff;
  padding: 24px;
  border-radius: 16px;
}

main .move_your_way_items .move_your_way_item .mywi_cover {
  max-width: 286px;
  max-height: 227px;
  overflow: hidden;
  margin-bottom: 16px;
}

main .move_your_way_items .move_your_way_item .mywi_cover a {
  display: block;
}

main .move_your_way_items .move_your_way_item .mywi_cover a img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

main .move_your_way_items .move_your_way_item .mywi_header {
  display: flex;
  gap: 16px;
  justify-content: space-between;
}

main .move_your_way_items .move_your_way_item .mywi_header p {
  font-size: 16px;
}

main .move_your_way_items .move_your_way_item .mywi_header p.offer {
  color: #7140EB;
  background: #F1EEF8;
  border-radius: 20px;
  padding: 1px 10px;
}

main .move_your_way_items .move_your_way_item .mywi_title_subtitle {
  margin: 24px 0px;
}

main .move_your_way_items .move_your_way_item .mywi_title_subtitle h4 {
  margin: 0px 0px 16px 0px;
}

main .move_your_way_items .move_your_way_item .mywi_title_subtitle h4 a {
  font-size: min(29px, max(15px + 2vw));
  color: #49464F;
  text-decoration: none;
}

main .move_your_way_items .move_your_way_item .mywi_title_subtitle p {
  color: #49464F;
}

main .move_your_way_items .move_your_way_item .mywi_btn a {
  justify-content: center;
  width: 100%;
}

body {
  font-family: "r-regular", sans-serif;
}

@media screen and (max-width: 1120px) {
  main .how_its_work_section .mini_container .how_its_work_cover {
    width: 70vw;
  }

  main .how_its_work_section .mini_container .how_its_work_cover img {
    width: inherit;
  }
}

@media screen and (max-width: 1024px) {
  main .move_your_way_items {
    flex-wrap: wrap;
  }
}

@media screen and (max-width: 768px) {
  .light_text_title,
  .dark_text_title {
    margin: 0px 0px 48px 0px !important;
    text-align: center;
  }

  .subtitle {
    text-align: center;
  }

  header nav {
    min-height: 100px;
  }

  header nav .nav_mobile {
    display: block;
  }

  header nav .nav_right {
    position: fixed;
    flex-direction: column;
    align-items: flex-start;
    top: 0;
    left: -5000px;
    width: 250px;
    height: 100vh;
    background: white;
    gap: 32px;
    transition: left 0.4s;
  }

  header nav .nav_right.active_nav {
    transition: left 0.4s;
    left: 0px;
  }

  header nav .nav_right .nav_menu {
    flex-direction: column;
    gap: 0px;
    width: 100%;
    border-bottom: 0.5px solid #eee;
  }

  header nav .nav_right .nav_menu li a {
    padding: 0px 30px;
    color: #2E2C34;
  }

  header nav .nav_right .nav_menu li a:hover {
    color: #fff;
  }

  header nav .nav_right .nav_menu li a:last-child {
    border-bottom: none;
  }

  header nav .nav_right .nav_lang {
    padding: 0px 30px;
  }

  header nav .nav_right .nav_lang a {
    color: #2E2C34;
  }

  header nav .nav_right .nav_btn {
    padding: 0px 30px;
    width: 100%;
  }

  header nav .nav_right .nav_btn a {
    width: 100%;
    text-align: center;
  }

  header {
    min-height: unset;
  }

  header .header_content {
    padding: 64px 10px 64px 10px;
  }

  header .header_content p {
    margin: 32px 0px;
  }

  main .how_its_work_section .mini_container .how_its_work_cover {
    display: none;
  }

  main .how_its_work_section .mini_container .how_its_work_list {
    text-align: center;
  }

  main .how_its_work_section .mini_container .how_its_work_list a {
    margin: 0 auto;
  }
}